import { PackageDimensionModel } from './package-dimension-model'

export class LineItemModel {
    totalWeight: number;
    packageDimensions: PackageDimensionModel[] = [];
    freightClass: string = "70";
    packageType: string = "PLT";
    totalPackages: number = 1;
    totalPieces: number = 1;
    description: string = "TEST LINE ITEM";
    stackable: boolean = false;


    // nmfcItemCode: string;
    // nmfcSubCode: string;

    //  Need to create this.
    // hazmatDetail: HazmatModel[]

    constructor() {
        var pd = new PackageDimensionModel();
        pd.height = 30;
        pd.width = 40;
        pd.length = 40;

        this.packageDimensions.push(pd);
    }
}



