import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddressModel } from '../../models/address-model';
import { LineItemModel } from '../../models/line-item-model';

import { ZipCodeProvider } from '../../providers/zip-code/zip-code';
/**
 * Generated class for the NewQuotePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
    name: 'NewQuote'
})
@Component({
  selector: 'page-new-quote',
  templateUrl: 'new-quote.html',
})
export class NewQuotePage {

  origin: AddressModel = new AddressModel();
  destination: AddressModel = new AddressModel();
  lineItems: LineItemModel[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, public zipCodeProvider:ZipCodeProvider) {
    var li = new LineItemModel();
    this.lineItems.push(li);

    this.origin.postalCode = '60606';
    this.destination.postalCode = '92126';

    this.zipCodeProvider.getCityByZipCode(this.origin.postalCode).subscribe(
      data => {console.log(`DATA: ${JSON.stringify(data)}`)},
      err => {console.log(`ERROR: ${JSON.stringify(err)}`)},
      () => console.log('Done loading zipcode')
    );


    this.zipCodeProvider.getCityByZipCode(this.destination.postalCode).subscribe(
      data => {console.log(`DATA: ${JSON.stringify(data)}`)},
      err => {console.log(`ERROR: ${JSON.stringify(err)}`)},
      () => console.log('Done loading zipcode')
    );
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NewQuotePage');
  }

}



