export class AddressModel {
    postalCode: string;
    addressLines: string[] = [];
    city: string;
    state: string;
    county: string = 'US';

    constructor() {
    }

    addAddressLine(item:string) {
        this.addressLines.push(item);
    }

    clearAddressLines() {
        this.addressLines = [];
    }
}



